import { Injectable } from '@angular/core';
import { Router, Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { take, map } from 'rxjs/operators';

import { InfoUnitService } from './info-unit.service';
import { Library, Helper } from '@core/index';

@Injectable()
export class InfoUnitResolver implements Resolve<any[]> {
  constructor(
      private infoUnitService: InfoUnitService, 
      private router: Router
  ) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any[]> {
    Library.showloading();
    return this.infoUnitService.getDataSelect().pipe(
      take(1),
      map(rs => {
        Library.hideloading();
        if (rs.status == "success") {
          return rs.data;
        } else { // error
          Library.notify(rs.message, "error");
          this.router.navigate(['/']);
          return null;
        }
      })
    );
  }
}