import { Component, OnInit, ViewChildren } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { AuthService, SocketService, Library, VStringService, Helper } from '@core/index';
import { InfoUnitService, InfoUnit } from '@modules/info-unit/shared';
import { DxFormComponent } from 'devextreme-angular';
import { pattern } from '@config/pattern';
import { Subscription } from 'rxjs/Subscription';
import { VSelectBoxService } from '@core/library/public/v-select-box.service';
import * as moment from 'moment';
import { TranslateService } from '@ngx-translate/core';
import { environment } from '@environments/environment';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { BsModalService } from 'ngx-bootstrap/modal';
import { PopupInfoUnitServiceComponent } from '@shared/index';
@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {
  @ViewChildren(DxFormComponent) form: DxFormComponent;

  accountId: any;
  infoUnit: InfoUnit = new InfoUnit();
  cities: any;
  taxDepartments: any;
  taxDepartmentByCity: any;
  groupUnits = [];
  declarationForms = [];
  reportPeriods = [];
  emailPattern = pattern.email;
  phonePattern = pattern.phone;
  taxCodePattern = pattern.tax_code;
  ioSignature: Subscription;
  tax_code;
  isDisTaxDepartments: any;
  _translate: any;
  email = '';
  emailValue = '';
  disableTaxHCM: boolean = false;
  isHCM: boolean = false;
  emailComparison = () => {
    return this.form['_results'][1].instance.option('formData').email;
  };
  optionsXML: any;
  // Xuất hóa đơn mặc định hoặc theo thuế HCM
  TaxXMLHCM: boolean = false;
  valueOptionXml: any;
  bsModalSignRef: BsModalRef;
  bsModalRef: BsModalRef;
  constructor(
    private authService: AuthService,
    private infoUnitService: InfoUnitService,
    private route: ActivatedRoute,
    private socketService: SocketService,
    public translate: TranslateService,
    private modalService: BsModalService,
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
  }

  ngOnInit() {
    this.optionsXML =
      [
        { name: this._translate.CONTROLL.LABEL.in_the_old_format, code: 1 },
        { name: this._translate.CONTROLL.LABEL.tax_department_of_HCM, code: 2 },
        { name: this._translate.CONTROLL.LABEL.notice_no_92_TB_TCT, code: 3 }
      ];
    this.groupUnits = VSelectBoxService._groupUnits(this._translate);
    this.declarationForms = VSelectBoxService._declarationForms(this._translate);
    this.reportPeriods = VSelectBoxService._reportPeriod(this._translate);
    this.getAll();
    this.route.data.subscribe((data: any) => {
      this.cities = data.lists.DM_TINH_THANH;
      this.taxDepartments = data.lists.DM_CO_QUAN_THUE;
      // let a = JSON.stringify(data.lists.DM_CO_QUAN_THUE);
    });

    this.ioSignature = this.socketService.getData().subscribe(rs => {
      const data = JSON.parse(rs.toString());
      if (data.Status === 'success') {
        const infor = data.Data;
        const currentUser = JSON.parse(localStorage['currentUser']);
        if (infor.MST === currentUser.account.tax_code || (currentUser.account.tax_code.length === 10 && +currentUser.account.tax_code >= 9999999900) || !environment.isCheckSignature) {
          this.infoUnit.cert_serial = infor.SerialNumber;
          this.infoUnit.cert_provider = this.convertIssuer(infor.Issuer);
          this.infoUnit.cert_valid_from = this.convertDate(infor.DateFrom);
          this.infoUnit.cert_valid_to = this.convertDate(infor.DateTo);
          this.infoUnit.icc_token = '';
          this.infoUnit.cert_content = infor.CertContent;
        } else {
          Library.notify('Thông tin chữ kỹ số không đúng, xin mời chọn lại', 'error');
        }

      } else {
        alert(data.Description);
      }
    });
  }

  getAll() {
    this.accountId = this.authService.getCurrentUser().account.id;
    if (this.accountId) {
      this.infoUnitService.getInfo(this.accountId).subscribe((res) => {
        delete res.data.account.van_account_info;
        this.infoUnit = res.data.account;
        this.emailValue = this.infoUnit.email;
        this.valueOptionXml = this.infoUnit.xml_type;
        // // Kiểm tra nếu là cục thuế HCM thì mở lựa chọn XMl HCM 
        if (this.infoUnit.tax_province_code != "701") {
          this.optionsXML = this.optionsXML.filter(p => p.code != 2);
          // this.isHCM = true;
          // if (this.infoUnit.time_used_xml_new)
          //   this.TaxXMLHCM = true;
          // else
          //   this.TaxXMLHCM = false;
          // if (this.TaxXMLHCM)
          //   this.disableTaxHCM = this.TaxXMLHCM;
        }
      });
    }
  }

  onCitiesSelected = (e) => {
    this.taxDepartmentByCity = [];
    if (e.event) {
      this.infoUnit.tax_department = '';
    }
    this.isDisTaxDepartments = false;
    this.getTaxDepartments(e.value);
  };

  getTaxDepartments(cityCode) {
    this.taxDepartments.forEach(element => {
      if (element.province_code === cityCode) {
        this.taxDepartmentByCity.push(element);
      }
    });
  }

  send() {
    // if (this.socketService.checkNull()) {
    //   this.socketService.connect();
    // }
    // setTimeout(() => {
    //   const res = this.socketService.sendSignatureInfor();
    //   if (res.status === 'error') {
    //     Library.notify(res.message, 'error');
    //     this.socketService.setNull();

    //   }
    // }, 100);
    const initialState = {
      taxcode: this.infoUnit.tax_code
    };
    this.bsModalSignRef = this.modalService.show(PopupInfoUnitServiceComponent, {
      backdrop: 'static',
      keyboard: true,
      class: 'signature modal-lg modal-margin-width',
      initialState
    });
    const subModal = this.modalService.onHide.subscribe(() => {
      if (this.bsModalSignRef.content.successSaveData) {
        const certificate = this.bsModalSignRef.content.successSaveData;
        const currentUser = JSON.parse(localStorage['currentUser']);
        if (certificate.MST === currentUser.account.tax_code || (currentUser.account.tax_code.length === 10 && +currentUser.account.tax_code >= 9999999900) || !environment.isCheckSignature) {
          this.infoUnit.cert_serial = certificate.SerialNumber;
          this.infoUnit.cert_provider = this.convertIssuer(certificate.Issuer);
          this.infoUnit.cert_valid_from = this.convertDate(certificate.DateFrom);
          this.infoUnit.cert_valid_to = this.convertDate(certificate.DateTo);
          this.infoUnit.icc_token = '';
          this.infoUnit.cert_content = certificate.CertContent;
        } else {
          Library.notify('Thông tin chữ kỹ số không đúng, xin mời chọn lại', 'error');
        }
      }
      subModal.unsubscribe();
    });
  }

  convertIssuer(str) {
    return str;
  }

  convertDate(str) {
    return moment(str).format('YYYY-MM-DD HH:mm:ss');
  }

  // xử lý submit
  onSubmit(e) {
    const currentUser = this.authService.getCurrentUser();
    this.infoUnit.submitted_person_name = this.infoUnit.name;
    this.infoUnit.xml_type = this.valueOptionXml;
    let account_type = currentUser.account.account_type;
    if (account_type == "HS-SV") {
      if (!this.infoUnit.time_study || this.infoUnit.time_study <= 0) {
        if (this.infoUnit.time_study <= 0)
          Library.notify("Số tháng/năm học phải lớn hơn 0", 'error');
        else
          Library.notify("Chưa nhập thông tin Số tháng/năm học", 'error');
        return;
      }
      if (!this.infoUnit.start_time_study || this.infoUnit.start_time_study <= 0) {
        if (this.infoUnit.start_time_study <= 0)
          Library.notify("Bắt đầu từ tháng phải lớn hơn 0", 'error');
        else
          Library.notify("Chưa nhập thông tin Bắt đầu từ tháng", 'error');
        return;
      }
      if (!this.infoUnit.end_time_study || this.infoUnit.end_time_study <= 0) {
        Library.notify("Đến tháng phải lớn hơn 0", 'error');
        return;
      }
    }
    console.log(this.infoUnit);
    const data = {
      'account': this.infoUnit
    };
    Library.showloading();
    this.infoUnitService.updateInfo(data, this.accountId).subscribe((response: any) => {
      if (response.status === 'success') {
        currentUser.account = response.data.account;
        localStorage['currentUser'] = JSON.stringify(currentUser);
        Library.notify('Câp nhật thông tin đơn vị thành công', 'success');
      } else {
        Library.notify(response.message, 'error');
      }
      Library.hideloading();
    });
  }

  inputChange($event: any) {
    if ($event.srcElement.name === 'time_study') {
      if ($event.srcElement.valueAsNumber && $event.srcElement.valueAsNumber > 0) {
        if ($event.srcElement.valueAsNumber > parseInt($event.srcElement.max, 10)) {
          Library.notify($event.srcElement.validationMessage, 'error');
          this.infoUnit.time_study = 0;
        } else {
          this.infoUnit.time_study = $event.srcElement.valueAsNumber;
          this.sumEndStudy();
        }
      } else {
        this.infoUnit.time_study = 0;
        this.infoUnit.start_time_study = 0;
        this.infoUnit.end_time_study = 0;
      }
    }
    if ($event.srcElement.name === 'start_time_study') {
      if ($event.srcElement.valueAsNumber && $event.srcElement.valueAsNumber > 0) {
        if ($event.srcElement.valueAsNumber > parseInt($event.srcElement.max, 10)) {
          Library.notify($event.srcElement.validationMessage, 'error');
          this.infoUnit.start_time_study = 0;
        } else {
          this.infoUnit.start_time_study = $event.srcElement.valueAsNumber;
          this.sumEndStudy();
        }
      } else {
        this.infoUnit.start_time_study = 0;
        this.infoUnit.end_time_study = 0;
      }
    }
  }

  sumEndStudy() {
    if (this.infoUnit.time_study && this.infoUnit.start_time_study) {

      let _dteCurrent: any = moment();
      var _dte = new Date(_dteCurrent.year(), this.infoUnit.start_time_study - 1, 1);
      let tp = this.infoUnit.time_study - 1;
      let mont = _dte.getMonth();
      _dte.setMonth(mont + tp);
      this.infoUnit.end_time_study = _dte.getMonth() < 12 ? _dte.getMonth() + 1 : 1;
      //  this.infoUnit.end_time_study = this.infoUnit.time_study - (13 - this.infoUnit.start_time_study);
    }
  }
  taomaxacthuc()
  {
    let verify_code = Helper.makeid().toUpperCase().replace(/-/g,"");
    this.infoUnit.verify_code = verify_code;
  }
}
