import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { IndexComponent } from './components/index/index.component';

import { DxTreeListModule } from 'devextreme-angular';
//library
import { devextremeModule } from '@core/index';

//general
import { routing } from './info-unit.routing';
import { SharedModule } from '@shared/index';
import { InfoUnitService, InfoUnitResolver } from './shared';
import { vTranslateModule } from '@core/module/v-translate.module';
import { PopupInfoUnitServiceComponent } from '@shared/index';

@NgModule({
  imports: [
    CommonModule,
    routing,
    devextremeModule,
    DxTreeListModule,
    SharedModule,
    vTranslateModule
  ],
  declarations: [IndexComponent],
  entryComponents: [
    PopupInfoUnitServiceComponent
  ],
  providers: [InfoUnitService, InfoUnitResolver]
})
export class InfoUnitModule { }